import React from "react"
import { Link } from "gatsby"

import ColorModeSwitcher from "components/modules/colorModeSwitcher"
import IndexPage from "Project/pages/index"

export default function Home() {
  return (
    <>
      <ColorModeSwitcher text="Switch color mode" />
      <IndexPage />
      <hr></hr>
      <Link to="/blog">Blog</Link>
    </>
  )
}
