import React from "react"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"

export default function IndexPage() {
  return (
    <StaticQuery
      query={graphql`
        query HeadingQuery {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => (
        <header>
          <h1>{data.site.siteMetadata.title}</h1>
          <h2>Project Index Page Here</h2>
        </header>
      )}
    />
  )
}
